.rounded-circle{
    border-radius: 50%;
    background-color: var(--logoBackground);
    border: none;
}

.market-table .market-table-content .showMoreRow {
    border-bottom: 0px;
}

.market-table .market-table-content .showMore {
    text-align: center;
    color: var(--showMore);
    font-size: 10px;
    font-weight: bold;
}

.supply-apy{
    display: flex;
    align-items: center;
    justify-content: right;
}

.bpro-asset{
    margin-left: 100px;
    text-align: right;
}

.bpro-asset div{
    padding-left: calc(30px + 1rem);
}

@media (min-width: 900px){
    .markets{
        display: flex;
        justify-content: center;
    }

    .market-container{
        margin-top: 2rem;
        border-bottom: 0;
        padding: 0 .5rem;
        max-width: 800px;
        margin-left: 0;
        margin-right: 0;
    }

    .market-content{
        width: 100%;
    }

}

.bodh-reward{
    display: flex;
    align-items: center;
    justify-content: center;
}
