body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes GradientBackgroundAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-moz-keyframes GradientBackgroundAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes GradientBackgroundAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.loading-bar {
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background-size: 800px 104px;
  background: linear-gradient( to right, rgba(255, 255, 255, 0.1) 8%, rgba(255, 255, 255, 0.118) 18%, rgba(255, 255, 255, 0.1) 33%);
  margin-top: 10px;
  height: 1.5rem;
  width: 75%;
}

@keyframes placeHolderShimmer {
  0% {
      background-position: -468px 0;
  }
  100% {
      background-position: 468px 0;
  }
}

.claim-button {
  background-color: transparent; /* Green */
  border: 1px solid var(--buttonColor);
  color: var(--text);
  margin: 0 5px;
  /* display: flex; */
  height: 25px;
  min-width: 70px;
  max-width: 90px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
  /* position: relative; */
  /* align-items: center;
  justify-content: center; */
}

.claim-button:hover {
  background-color: var(--buttonColor);
  color: var(--buttonHover2);
}